import React, { useEffect, useState } from 'react'
import { back_color, button_color } from './helper/colors'
import grad from './gradient.png'
import "./App.css";
import { useLocation, useNavigate } from 'react-router-dom';

const height = window.innerHeight;
const width = window.innerWidth;

// https://webgradients.com/public/webgradients_png/032%20Sharpeye%20Eagle.png

export default function First() {

    const navigate = useNavigate();

    const [touched1, setTouched1] = useState(false);
    const [touched2, setTouched2] = useState(false);

    const toggleTouched1 = () => {
        setTouched1(!touched1)
    }

    const handleMouseUp1 = () => {
        // Handle smooth animation when clicking without holding
        setTimeout(() => {
            setTouched1(false)
        }, 150);
    }

    const toggleTouched2 = () => {
        setTouched2(!touched2)
    }

    const handleMouseUp2 = () => {
        // Handle smooth animation when clicking without holding
        setTimeout(() => {
            setTouched2(false)
        }, 150);
    }

    return (
        <div src={grad} style={{ backgroundImage: "url(" + "https://webgradients.com/public/webgradients_png/032%20Sharpeye%20Eagle.png" + ")", display: 'flex', justifyContent: 'center', backgroundColor: back_color, alignItems: 'center', height: height, flexDirection: 'column' }}>

            <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                <text style={{ textAlign: 'center', fontWeight: '500', letterSpacing: 1.02, fontSize: 18 }}>Choose anyone of these .</text>
            </div>

            <div onClick={() => navigate("/vt")} className={touched1 ? 'btn touched' : 'btn'} onMouseDown={toggleTouched1} onMouseUp={handleMouseUp1} style={{ cursor: 'pointer', marginTop: 25, display: 'flex', justifyContent: 'center', borderRadius: 4, padding: 10, width: 430, backgroundColor: button_color, padding: 8, alignItems: 'center', alignContent: 'center', }}>
                <text style={{ color: 'white', fontWeight: '400', textAlign: 'center' }}> Voice to Text </text>
            </div>

            <div onClick={() => navigate("/tv")} className={touched2 ? 'btn touched' : 'btn'} onMouseDown={toggleTouched2} onMouseUp={handleMouseUp2} style={{ cursor: 'pointer', marginTop: 10, display: 'flex', justifyContent: 'center', borderRadius: 4, padding: 10, width: 430, backgroundColor: button_color, padding: 8, alignItems: 'center', alignContent: 'center', }}>
                <text style={{ color: 'white', fontWeight: '400', textAlign: 'center' }}> Text to Voice </text>
            </div>

        </div>
    )
}
