
import './App.css';
import {
    BrowserRouter,
    Routes, //replaces "Switch" used till v5
    Route,
    HashRouter
} from "react-router-dom";
import React, { useState, useEffect } from 'react';
import Text_to_voice from './T_to_v/App';
import First from './First';
import Voice_to_text from './V_to_t/App';

export default function App() {

    return (
        <div className="App">
            <HashRouter>
                <Routes>

                    <Route path="/" element={<First />} />
                    <Route path="/tv" element={<Text_to_voice />} />
                    <Route path="/vt" element={<Voice_to_text />} />

                </Routes>
            </HashRouter>
        </div>
    );
}