//import '../App.scss';
import React from 'react';
import SpeechRecognition, { useSpeechRecognition } from 'react-speech-recognition';
import { back_color, button_color } from '../helper/colors';
//import { Card } from '@mui/material';

const width = window.innerWidth;

function Voice_to_text() {

    const { transcript, listening, resetTranscript, browserSupportsSpeechRecognition,SpeechRecognitionPolyfill } = useSpeechRecognition();

    //SpeechRecognition.applyPolyfill(SpeechRecognitionPolyfill)

    if (!browserSupportsSpeechRecognition) {
        return <span>Browser doesn't support speech recognition. Plz use Google Chrome browser to use this feature.</span>;
    }

    return (
        <div style={{ display: 'flex', justifyContent: 'center', flexDirection: 'column' }}>

            <div style={{ display: 'flex', justifyContent: 'center', marginTop: 40, width: 0.80 * width, height: 150, alignSelf: 'center' }} elevation={3} >
                <div style={{ display: 'flex', justifyContent: 'center', borderColor: button_color, width: 0.80 * width, alignSelf: 'center', alignItems: 'center' }}>
                    <text style={{ textAlign: 'center' }}>{transcript == "" ? "Say something" : transcript} </text>
                </div>
            </div>

            <div onClick={SpeechRecognition.startListening({ continuous: true })} style={{ marginTop: 50, cursor: 'pointer', width: 180, padding: 10, borderRadius: 6, backgroundColor: button_color, alignSelf: 'center' }}>
                <text style={{ color: 'white', fontWeight: '600' }}>Listen continuously</text>
            </div>

            <div onClick={resetTranscript} style={{ cursor: 'pointer', width: 180, padding: 10, borderRadius: 6, marginTop: 5, backgroundColor: button_color, alignSelf: 'center' }}>
                <text style={{ color: 'white', fontWeight: '600' }}>Reset</text>
            </div>

            <p>Microphone: {listening ? 'on' : 'off'}</p>

        </div>
    );

};

export default Voice_to_text;