import React from 'react'
import "../App.css"
import { useState, useEffect, useRef } from "react"
import 'toolcool-range-slider';
import { back_color, button_color, login_color, welcom_to_color } from "../helper/colors";
import { Dropdown, Selection } from 'react-dropdown-now';
import 'react-dropdown-now/style.css';

const height = window.innerHeight;
const width = window.innerWidth;

function Text_to_voice() {

  const rangeSliderRef = useRef();
  const rangeSliderRef1 = useRef();
  const rangeSliderRef2 = useRef();

  const [curr_state, setCurrState] = useState("Waiting to be played !");

  const [curr_voice, setCurrVoice] = useState(0);

  const [selection, setSelection] = useState([]);

  const [ourText, setOurText] = useState("")
  const msg = new SpeechSynthesisUtterance()

  const [voices, setVoices] = useState([]);

  const [rate, setRate] = useState(0.5);
  const [pitch, setPitch] = useState(1);
  const [volume, setVolume] = useState(1);

  useEffect(() => {
    if (voices.length == 0) {
      voiceList();
    }
  });

  useEffect(() => {

    const slider = rangeSliderRef.current;

    const onChange = (evt) => {
      const customEvent = evt;
      const value = evt.detail.value;
      setRate(value);
    };

    slider?.addEventListener('change', onChange);

    return () => {
      slider?.removeEventListener('change', onChange);
    };
  }, []);

  useEffect(() => {

    const slider = rangeSliderRef1.current;

    const onChange = (evt) => {
      const customEvent = evt;
      const value = evt.detail.value;
      setPitch(value);
    };

    slider?.addEventListener('change', onChange);

    return () => {
      slider?.removeEventListener('change', onChange);
    };
  }, []);

  useEffect(() => {

    const slider = rangeSliderRef2.current;

    const onChange = (evt) => {
      const customEvent = evt;
      const value = evt.detail.value;
      setVolume(value);
    };

    slider?.addEventListener('change', onChange);

    return () => {
      slider?.removeEventListener('change', onChange);
    };
  }, []);

  const speechHandler = (msg) => {
    msg.text = ourText;
    console.log(voices);
    msg.voice = voices[curr_voice];
    // max 1
    msg.volume = volume;
    // max 2 
    msg.rate = rate;
    // max 2
    msg.pitch = pitch;
    window.speechSynthesis.speak(msg)
  }

  const pause = () => {
    window.speechSynthesis.pause();
  }

  const resume = () => {
    window.speechSynthesis.resume();
  }

  const stop = () => {
    window.speechSynthesis.cancel();
  }

  const voiceList = () => {
    let arr = window.speechSynthesis.getVoices();
    let temp_arr = [];
    for (let i = 0; i < arr.length; i++) {
      temp_arr.push(arr[i].name);
    }
    console.log(arr);
    setVoices([...arr]);
    setSelection([...temp_arr]);
  }

  const voice_select = (val) => {
    //console.log(val);
    for (let i = 0; i < selection.length; i++) {
      if (selection[i] === val.value) {
        console.log(i);
        setCurrVoice(i);
        break;
      }
    }
  }

  msg.addEventListener('pause', (event) => {
    setCurrState("Paused")
  });

  msg.addEventListener('end', (event) => {
    setCurrState("Finished")
  });

  msg.addEventListener('start', (event) => {
    setCurrState("Playing")
  });

  msg.addEventListener('resume', (event) => {
    setCurrState("Playing")
  });
 

  return (
    <div style={{ display: 'flex', flexDirection: 'row',  backgroundColor: back_color, height: height }}>


      <div style={{ height: 200, display: 'flex', flexDirection: 'column', padding: 10, marginLeft: 10 ,marginTop:35}}>

        <Dropdown
          placeholder="Select voice"
          className="my-className"
          options={selection}
          onSelect={(value) => voice_select(value)} // always fires once a selection happens even if there is no change
        />

        <div style={{ marginTop: 30, width: 350, alignSelf: 'center', alignItems: 'center', display: 'flex', flexDirection: 'row', justifyContent: 'space-around' }}>

          <div style={{ width: 70, backgroundColor: button_color, alignItems: 'center', justifyContent: 'center', padding: 10, borderRadius: 8 }}>
            <text style={{ color: 'white', fontWeight: '700' }}>Speed</text>
          </div>

          <text style={{ color: 'green' }}>{rate}</text>
          <text></text>

          <toolcool-range-slider
            ref={rangeSliderRef}
            min="0"
            max="2"
            value="0.5"
            slider-width="200px"
            slider-height="0.5rem"
            pointer-width="1.5rem"
            pointer-height="1.5rem"
            slider-bg="#6AD3BA"
            slider-bg-hover="#3F8A8A"
            pointer-border-hover="1px solid #79D6C0"
            pointer-border-focus="1px solid #79D6C0" />
        </div>

        <div style={{ width: 350, marginTop: 10, alignSelf: 'center', alignItems: 'center', display: 'flex', flexDirection: 'row', justifyContent: 'space-around' }}>

          <div style={{ width: 70, backgroundColor: button_color, alignItems: 'center', justifyContent: 'center', padding: 10, borderRadius: 8 }}>
            <text style={{ color: 'white', fontWeight: '700' }}>Pitch</text>
          </div>

          <text style={{ color: 'green' }}>{pitch}</text>
          <text></text>

          <toolcool-range-slider
            ref={rangeSliderRef1}
            min="0"
            max="2"
            value="0.5"
            slider-width="200px"
            slider-height="0.5rem"
            pointer-width="1.5rem"
            pointer-height="1.5rem"
            slider-bg="#6AD3BA"
            slider-bg-hover="#3F8A8A"
            pointer-border-hover="1px solid #79D6C0"
            pointer-border-focus="1px solid #79D6C0" />
        </div>

        <div style={{ width: 350, marginTop: 10, alignSelf: 'center', alignItems: 'center', display: 'flex', flexDirection: 'row', justifyContent: 'space-around' }}>

          <div style={{ width: 70, backgroundColor: button_color, alignItems: 'center', justifyContent: 'center', padding: 10, borderRadius: 8 }}>
            <text style={{ color: 'white', fontWeight: '700' }}>Volume</text>
          </div>

          <text style={{ color: 'green' }}>{volume}</text>
          <text></text>

          <toolcool-range-slider
            ref={rangeSliderRef2}
            min="0"
            max="1"
            value="1"
            slider-width="200px"
            slider-height="0.5rem"
            pointer-width="1.5rem"
            pointer-height="1.5rem"
            slider-bg="#6AD3BA"
            slider-bg-hover="#3F8A8A"
            pointer-border-hover="1px solid #79D6C0"
            pointer-border-focus="1px solid #79D6C0" />
        </div>

      </div>

      <div style={{ display: 'flex', flexDirection: 'column', marginLeft: 150 }}>
        <div style={{ display: 'flex', position: 'absolute', top: 50, flexDirection: 'column' }}>

          <div style={{ display: 'flex', flexDirection: 'row', width: 350, justifyContent: 'space-between', marginBottom: 10, alignSelf: 'center' }}>
            <div onClick={() => speechHandler(msg)} style={{ cursor: 'pointer', width: 80, backgroundColor: login_color, padding: 10, alignItems: 'center', borderRadius: 6 }}>
              <text style={{ color: 'black', fontWeight: '700', textAlign: 'center' }}>Start</text>
            </div>
            <div onClick={() => pause()} style={{ cursor: 'pointer', width: 80, backgroundColor: login_color, padding: 10, alignItems: 'center', borderRadius: 6 }}>
              <text style={{ color: 'violet', fontWeight: '800', textAlign: 'center' }}>Pause</text>
            </div>
            <div onClick={() => resume()} style={{ cursor: 'pointer', width: 80, backgroundColor: login_color, padding: 10, alignItems: 'center', borderRadius: 6 }}>
              <text style={{ color: 'green', fontWeight: '600', textAlign: 'center' }}>Resume</text>
            </div>
          </div>

          <input
            type='text'
            value={ourText}
            placeholder='Enter text here'
            onChange={(e) => setOurText(e.target.value)}
            multiple={true}
            style={{ height: 80, padding: 5, borderRadius: 6, borderColor: button_color, borderWidth: 1, width: 350 }}
          />

          <text style={{marginTop:15,textAlign:'center',color:"gray",fontWeight:'700'}}>{curr_state}</text>

          <div onClick={() => stop()} style={{ marginTop: 30, cursor: 'pointer', width: 80, backgroundColor: login_color, padding: 10, alignItems: 'center', borderRadius: 6, alignSelf: 'center' }}>
            <text style={{ color: 'green', fontWeight: '600', textAlign: 'center' }}>Stop</text>
          </div>

        </div>
      </div>

    </div>
  )
}

export default Text_to_voice


